//@flow
import React from "react";
import PostCard from "./PostCard";
import withStyles from "@material-ui/core/styles/withStyles";
import Typography from "@material-ui/core/Typography";

const style = {
  article: { padding: 16, maxWidth: 720 },
  header: { marginBottom: 32 }
};

class PostListing extends React.Component<{}> {
  static renderPost = ({ excerpt, frontmatter }) => {
    return (
      <PostCard
        key={frontmatter.path}
        title={frontmatter.title}
        section={frontmatter.section}
        link={frontmatter.path}
        excerpt={excerpt}
      />
    );
  };

  render() {
    const { posts, listingName, classes } = this.props;

    return (
      <article className={classes.article}>
        <header className={classes.header}>
          <Typography variant="headline" component="h2">
            {listingName}
          </Typography>
        </header>
        {posts.map(PostListing.renderPost)}
      </article>
    );
  }
}

export default withStyles(style)(PostListing);



